export default {
	data() {
		return {
			Contactpersons: [],
			Contactperson: {}, //默认联系人
			BUY:[],//商品
			
		};
	},
	mounted() {
		var asd = this.$store.state.ShoppingCartasd
		// console.log(this.$store.state.Buy) //商品
		this.BUY = this.$store.state.Buy 
		uni.request({
			url: this.$store.state.url + 'CRM/GetCusLinkManMini',
			method: 'GET',
			data: {
				cusId: this.$store.state.Customercode, //客户id
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				if (JSON.parse(res.data).code == 0 || JSON.parse(res.data).Code == 0) {
					// console.log(JSON.parse(res.data).Data.Dtos)
					this.Contactpersons = JSON.parse(res.data).Data.Dtos
					for (var i = 0; i < this.Contactpersons.length; i++) {
						if (this.Contactpersons[i].Default) {
							this.Contactperson = this.Contactpersons[i]
						}
					}
					if (!this.Contactperson) {
						console.log('没有默认联系人')
						this.Contactperson = this.Contactpersons[0]
					}
					console.log(this.Contactperson)
					//切换联系人
					if(this.$store.state.lianxir.Name){
						this.Contactperson = this.$store.state.lianxir
						console.log(this.Contactperson)
					}
				} else {
					uni.showToast({
						title: JSON.parse(res.data).Data,
						icon: "none"
					})
				}
			}
		});
		
		
	},
	onBackPress(open){
		console.log(open)
	},
	methods: {
		//付款
		fukuan:function(){
			console.log(this.Contactperson)
			console.log(this.BUY)
			
			
			
			
		},
		//切换联系人
		huan(){
			
			uni.navigateTo({ url: "./qielxr", });
		}
	}
}
